<template>
	<view>
		<!-- 未登录 -->
		<template v-if="!loginStatus">
			<view>
				未登录
			</view>
			<image src="avatar" class="size-100 rounded-circle"></image>
		</template>
		<!-- 登录 -->
		<template v-else>
			<!-- 头像区域 -->
			<view class="flex align-center p-2">
				<image :src="avatar" class="size-100 rounded-circle"></image>
				<view class="flex-1 flex flex-column px-3">
					<text class="font-lg font-weight-bold text-muted">{{nickname}}</text>
					<view>
						<text class="mr-2">总帖数</text>
						<text class="text-warning">{{posts}}</text>
						<text class="mx-2">今日发帖</text>
						<text class="text-warning">{{dynamics}}</text>
					</view>
				</view>

				<text class="iconfont icon-jinru"  @tap="open('login')></text>
			</view>
			<!-- 数据区域 -->
			<view class="flex align-center p-2">
				<view class="flex flex-column flex-1 align-center justify-center">
					<text class="text-pink mb-1">{{posts}}</text>
					<text class="font-sm text-muted">帖子</text>
				</view>
				<view class="flex flex-column flex-1 align-center justify-center">
					<text class="text-pink mb-1">{{dynamics}}</text>
					<text class="font-sm text-muted">动态</text>
				</view>
				<view class="flex flex-column flex-1 align-center justify-center">
					<text class="text-pink mb-1">{{comments}}</text>
					<text class="font-sm text-muted">评论</text>
				</view>
				<view class="flex flex-column flex-1 align-center justify-center">
					<text class="text-pink mb-1">{{fans}}</text>
					<text class="font-sm text-muted">粉丝</text>
				</view>
			</view>

			<!-- banner区域 -->
			<view class="p-2">
				<image src="../../static/img/banner1.jpg" mode="aspectFill" class="w-100"></image>
			</view>

			<!-- 列表区域 -->
			<view>
				<uni-list-item showExtraIcon title="浏览历史">
					<text slot="icon" class="iconfont icon-liulan text-body font-md">
					</text>
				</uni-list-item>
				<uni-list-item showExtraIcon title="会员VIP">
					<text slot="icon" class="iconfont icon-huiyuanvip text-body font-md">
					</text>
				</uni-list-item>
				<uni-list-item showExtraIcon title="帖子审核">
					<text slot="icon" class="iconfont icon-keyboard text-body font-md">
					</text>
				</uni-list-item>
			</view>

		</template>
	</view>
</template>

<script>
	import uniListItem from '@/components/uni/uni-list-item/uni-list-item.vue';
	export default {
		components: {
			uniListItem
		},
		data() {
			return {
				loginStatus: true,
				nickname: 'radical',
				posts: 12,
				dynamics: 15,
				comments: 100,
				fans: 188

			}
		},
		computed: {
			avatar() {
				return this.loginStatus === true ? '/static/img/me.jpg' : '/static/img/nologin.jpeg'
			}
		},
		onNavigationBarButtonTap() {
			uni.showToast({
				title: '点击',
				duration: 2000
			})
		},
		methods: {
			open(path) {
				uni.navigateTo({
					url:`../${path}/${path}`,
				})
			}
		
		}
	}
</script>

<style>

</style>
